export default {
    routes: [],
    TestMethod() {
        alert(JSON.stringify(this.routes));
    },
    getListLink() {
        let s = [];
        let kq = [];
        //let routes = JSON.parse(JSON.stringify(this.$store.state.routes));
        let routes = this.routes;
        routes.forEach(x => {
            if (x.path != "*") {
                s.push(x);
            }
        })
        while (s.length != 0) {
            let layra = s.pop();
            if (layra.children == undefined) {
                kq.push(layra);
            } else {
                kq.push({ id: layra.id, label: layra.label, path: layra.path, auth: layra.auth });
            }
            let path = layra.path
            if (layra.children != undefined) {
                layra.children.forEach(y => {
                    if (y.path != "" && path != "/") {
                        y.path = path + "/" + y.path;
                    } else {
                        y.path = path + y.path;
                    }
                    s.push(y);
                })
            }
        }
        return kq;
    },
    itemToString(item) {
        let s = "";
        let keys = Object.keys(item);
        keys.forEach(x => {
            if (x != 'children') {
                s += `${x} -> ${item[x]} |`;
            }
        })
        return s;
    },
    getItem(arLink, id) {
        let index = arLink.findIndex(x => x.id == id);
        if (index < 0) console.log(`khong tim thay id : ${id}`);
        let kq = arLink[index];
        return kq;
    },
    getMenu() {
        //let routes = JSON.parse(JSON.stringify(this.$store.state.routes));
        let routes=this.routes;
        let ar = JSON.parse(JSON.stringify(routes[0].children));
        let arLink = this.getListLink();
        let ar2 = [];
        ar.forEach(x => {
            if (x.children == undefined) {
                let item = this.getItem(arLink, x.id);
                ar2.push(item);
            } else {
                let item2 = this.getItem(arLink, x.id);
                let children = [];
                x.children.forEach(y => {
                    let item = this.getItem(arLink, y.id);
                    children.push(item);
                })
                item2.children = children;
                ar2.push(item2);
            }
        })
        return ar2;
    },
    getChild(path){
        let ar=this.getMenu();
        let index=ar.findIndex(x=>x.path==path);
        if(index < 0){
            return []
        }else{
            let ar0=ar[index].children;
            let ar2=[];
            ar0.forEach(element => {
                if(element.path.includes(":")==false){
                    ar2.push(element);
                }
            });
            return ar2;
        }
    },
    getHor(){
        let ar=this.getMenu();
        let ar2=[];
        ar.forEach(x=>{
            ar2.push({
                id:x.id,
                path:x.path,
                label:x.label
            })
        })
        return ar2;
    }
}
